.App {
  text-align: center;
  background-color: whitesmoke;
  min-height: 120vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.content-cover {
  display: flex;
  flex-direction: column;
  width:1080px;
  align-items: center;
}

@media screen and (max-width:720px) {
  .App-header {
  flex-direction: row;
  }
}
