.details-wrapper {
  height: 275px;
  background: #f2f5f9;
  width: 300px;
  border-radius: 16px;
  margin-left: 30px;
  justify-content: space-between;
  align-items: center;
}

.button {
  margin-top: 16px;
  margin-left: 54px;
  padding: 10px 20px 10px 20px;
  border-radius: 16px;
  border: none;
  background: linear-gradient(
    90deg,
    rgba(250, 184, 45, 1) 4%,
    rgba(255, 44, 0, 1) 71%
  );
  color: white;
  display: flex;
  cursor: pointer;
}

.download {
  height: 32px;
  width: 32px;
}
.download-text {
  margin-top: 8px;
  padding-left: 8px;
}
